.rfid-tags-page {
  min-height: 100vh;
  background-color: #000000;
  padding-top: 80px;
}

.back-nav {
  padding: 1rem 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

.back-button {
  display: inline-flex;
  align-items: center;
  color: #00f2fe;
  text-decoration: none;
  font-size: 1.1rem;
  transition: all 0.3s ease;
}

.back-button span {
  margin-right: 0.5rem;
  font-size: 1.5rem;
}

.back-button:hover {
  color: #4facfe;
  transform: translateX(-5px);
}

.page-title {
  text-align: center;
  font-size: clamp(2rem, 5vw, 3rem);
  margin: 2rem 0;
  background: linear-gradient(45deg, #00f2fe, #4facfe);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 20px rgba(79, 172, 254, 0.5);
}

.tags-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 1rem;
}

@media screen and (max-width: 768px) {
  .back-nav {
    padding: 1rem;
  }
  
  .page-title {
    margin: 1rem 0;
    padding: 0 1rem;
  }
} 